import React from 'react'
import { chelseaProducts, flatBelts, linkBelts, roundBelts,slimWallet } from '../data'
import styled from 'styled-components'
import Product from './Product'
import ChelseaProductItem from './SubCategoryChelsea/ChelseaProductItem'
import FlatProductItem from './SubCategoryFlatBelts/FlatProductItem'
import LinkProductItem from './SubCategoryLinkBelts/LinkProductItem'
import RoundProductItem from './SubCategoryRoundBelts/RoundProductItem'
import SlimProductItem from './SubCategorySlimWallets/SlimProductItem'

const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`
const SlimProducts = () => {
  return (
    <Container>

    {slimWallet.map((item)=>(
    
    <SlimProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default SlimProducts