



import React from 'react';
import SendIcon from '@mui/icons-material/Send';
import styled from 'styled-components';

const Container = styled.div`
  height: 100%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  @media (min-width: 768px) {
    height: 60vh;
  }
`;

const Title = styled.h1`
  font-size: 3rem;
  margin-bottom: 2rem;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 4rem;
  }
`;

const Description = styled.p`
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 2rem;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 2rem;
  }
`;

const InputContainer = styled.div`
  width: 100%;
  max-width: 500px;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border: 1px solid lightgray;

  @media (min-width: 768px) {
    height: 5rem;
  }
`;

const Input = styled.input`
  border: none;
  flex: 1;
  font-size: 1.5rem;
  height: 100%;
  padding: 0 1rem;

  @media (min-width: 768px) {
    font-size: 2rem;
  }
`;

const Button = styled.button`
  flex: none;
  border: none;
  background-color: teal;
  color: white;
  font-size: 1.5rem;
  height: 100%;
  padding: 0 1rem;

  @media (min-width: 768px) {
    font-size: 2rem;
  }
`;

const Newsletter = () => {
  return (
    <Container>
      <Title>Newsletter</Title>
      <Description>Get timely updates from Popular Products</Description>
      <InputContainer>
        <Input placeholder="Your email" />
        <Button>
          <SendIcon />
        </Button>
      </InputContainer>
    </Container>
  );
};

export default Newsletter;