import React from 'react'
// import "../App.css"
// import { Container } from 'react-bootstrap'
import styled from 'styled-components'

const Container=styled.div`
flex:1;
margin:3px;
/* height:60px; */
/* height:70vh; */
position: relative;
`

const VideoText=styled.h1`
align-items: center;
text-align: center;
`




const VideoManufactured = () => {
  return (



<>
<Container>
  <VideoText>
    All our shoes are made to order by hand
  </VideoText>

<div class="row">
  <div class="column"></div>
  <div class="column"><div className='ratio ratio-4x3'>
        <iframe width="560" height="315" 

        src="https://www.youtube.com/embed/c7rQn6X2_No" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
         allowfullscreen></iframe>

        </div></div>
  <div class="column"></div>
</div>
</Container>
</>
  )
}

export default VideoManufactured