import React from 'react'
import {  formalProducts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import FormalProductScreen from './SubCategoryFormal/FormalProductScreen'
import FormalProductItem from './SubCategoryFormal/FormalProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`




const FormalProducts = () => {
  return (
    <Container>

    {formalProducts.map((item)=>(
    
    <FormalProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default FormalProducts