import React from 'react'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'
import styled from 'styled-components'

import "../App.css"
import { Navigate, useParams } from 'react-router-dom';
import {useLocation} from 'react-router-dom';

import { useNavigate } from 'react-router-dom';

import { Row, Col, Button } from "react-bootstrap";

const Image = styled.img`
width: 200px;

`;

const CustomOrderConfirm = ({navigation}) => {
  const navigate = useNavigate();
    const location=useLocation();
// console.log(location.state.price)

       const nameFromCustom=location.state.firstName
      const dynamicPriceFromCustom=location.state.dynamicPrice
      const phoneNumFromCustom=location.state.phoneNum
      const selectedCategoryFromCustom=location.state.selectedCategory
      const selectedSoleFromCustom=location.state.selectedSole
      const selectedColorFromCustom=location.state.selectedColor
      const selectedUpperFromCustom=location.state.selectedUpper
      const emailFromCustom=location.state.contactOrEmail


      console.log("email from custom "+emailFromCustom)
      console.log("price from custom "+dynamicPriceFromCustom)



const paymentPage=()=>{
  console.log(nameFromCustom)
navigate(`/payment/customshoepayment`,
{state:
  {
    dynamicPriceFromCustom,
    selectedCategoryFromCustom,
   phoneNumFromCustom,
   selectedSoleFromCustom,
   selectedColorFromCustom,
   selectedUpperFromCustom,
   emailFromCustom,
   nameFromCustom


  }

})
}



  return (
    <>
   <Navbar />
   <div>Thankyou your order has been confirmed</div>
    <p>Our team will contact you for all other details</p>
    
    <h4 className='checkoutTopHead'>Creative Handmade Creations</h4>
    <div className="checkoutMain">
       <div class="row">
       <div class="column1">
 <h4 className='orderConfirmHead'>

 Your order is confirmed
 </h4>
<p className='orderConfirmText'>
       
Our team will setup a WhatsApp group for your order which will include members of our Design
 and Production team so that we can discuss the details about your order and create some beautiful shoes for you.
 </p>

<Row>
      <Col xs={12} sm={6} md={4} lg={3}>
        <Button onClick={paymentPage} variant="primary" size="lg" block>
          {/* <FaCreditCard /> Go to Payment */}
          Go to payment
        </Button>
      </Col>
    </Row>

      </div>  
    <div className="column2">

     
<div className='sectionColumn2'>

{/* <Image src={image} className="checkoutimg"/> */}

<hr />
{/* <Image src={} className="checkoutimg"/> */}
<p className='total'>Subtotal

 <p className="priceSubtotal">Rs {}</p>
 <p className='productId'>
</p>

</p>


</div>


</div>

</div>







</div>
   <Footer />
</>
    )
}

export default CustomOrderConfirm