import React from 'react'
import Announcement from '../Announcement'
import Footer from '../Footer'
import Navbar from '../Navbar'

const SizeGuide = () => {
  return (
    <div>

    <Announcement />
    <Navbar />

    <h2 className='sizeGuide'>
 Size Guide
    </h2>

<p className='sizeguidetext'>
  Check following size standards for accuracy.
<ul>
  <li>UK</li>
  <li>USA</li>
  <li>EURO</li>
  <li>BATA</li>

</ul>

</p>

    <Footer />



    </div>
  )
}

export default SizeGuide