import React from 'react'
import { wholecutProducts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import WholeCutProductItem from './SubCategoryWholecut/WholeCutProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`


const WholecutProducts = () => {
  return (
    <Container>

{wholecutProducts.map((item)=>(

<WholeCutProductItem item={item} key={item.id} />
))}


    </Container>
  )
}

export default WholecutProducts