import React from 'react'
import { chelseaProducts, flatBelts, linkBelts, roundBelts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import ChelseaProductItem from './SubCategoryChelsea/ChelseaProductItem'
import FlatProductItem from './SubCategoryFlatBelts/FlatProductItem'
import LinkProductItem from './SubCategoryLinkBelts/LinkProductItem'
import RoundProductItem from './SubCategoryRoundBelts/RoundProductItem'

const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`
const RoundProducts = () => {
  return (
    <Container>

    {roundBelts.map((item)=>(
    
    <RoundProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default RoundProducts