import React from 'react'
import Announcement from '../Announcement'
import Footer from '../Footer'
import Navbar from '../Navbar'





const PrivacyPolicy = () => {
  return (
    <div>

  <Announcement />
  <Navbar />
  <div className='privacyPolicyMain'>
   <h3 className='privacyPolicyHead'>
   PRIVACY POLICY

   </h3>
   <br />
   
   <p className='privacyPolicyText'>
   Creative Handmade Creations is committed to ensuring that your personal information is used properly and is kept securely. This Privacy Policy explains how we will collect and use your personal information through a website [www.creativehandmadecreations.pk].
    Please read our Privacy Policy carefully to get a clear understanding of how we collect, use, protect 
   or otherwise handle your personal information. We will not sell or redistribute your information to anyone.

   </p>


  <h4 className='privacyPolicySecondText'>
  What personal information do we collect from the people that visit our website?


  </h4>
  <p className='privacyPolicyText'>

  When ordering or registering on our site, as appropriate, you may be asked to enter your name, email address, contact number, mailing address, credit card information or other details to help you with your experience.

  </p>

  <h4 className='privacyPolicySecondText'>
  When do we collect information

</h4>

<p className='privacyPolicyText'>
We collect information from you when you register on our site, place an order, subscribe to updates, leave a message on Customer Service, or enter information on our site.


</p>

<h4 className='privacyPolicySecondText'>


Use and storage of your personal information
</h4>
<p className='privacyPolicyText'>
We may use the information we collect from you when you register, make a purchase, sign up for our newsletter, respond to a survey or marketing communication, surf the website,
 or use certain other site features in the following ways:
</p>

 <ul>
  <li>To be able to contact you in the event of any problem with the delivery of your items</li>
  <li>To be able to send text message notifications of delivery status
</li>
  <li>To ask for ratings and reviews of services or products</li>
  <li>To allow us to better service you in responding to your customer service requests</li>
</ul>


  </div>




<Footer />
    </div>
  )
}

export default PrivacyPolicy