import React from 'react'
import { useState } from 'react'

import { useNavigate } from 'react-router-dom';
import Navbar from '../../src/components/Navbar'
import Footer from '../../src/components/Footer'
// import '../../src/App.css'
import { Link } from 'react-router-dom';
import './Register.css'



const Login = () => {
    const [email, setEmail] = useState('')
	const [password, setPassword] = useState('')
	const navigate = useNavigate();


	async function loginUser(event) {
		event.preventDefault()
		console.log(email)
		console.log(password)
		if(email=="" || password==""){
			alert("please fill values")
			return false;
		}
console.log("lets go to login")
		const response = await fetch('http://localhost:5000/api/login', {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: JSON.stringify({
				email,
				password,
			}),
		})
       
		const data = await response.json()
        console.log(data._id)
		if(data.status==200)
		{
			console.log("Data from login req is ")
			console.log(JSON.stringify(data));

alert("login success")
navigate('/account',

{state: {response: data}})
		}
		else{
			alert("login failed ensure you enter correct details")
		}
		
	}

const createUser=(e)=>{
	e.preventDefault();
	navigate("/register")
}

  return (
    <div>
		<Navbar />
		<div className='mainLogin'>

		<h4 className='loginUser'>Login</h4>

		{/* <div className='loginContainer'> */}
			<form className='loginForm '>
				{/* <input
					value={email}
					onChange={(e) => setEmail(e.target.value)}
					type="email"
					placeholder="Email"
					// className="loginInput"
				/> */}

<input
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            type="email"
            placeholder="Email"
            required="true"
			className="loginInput"
        />

				<br />
				<input
					value={password}
					onChange={(e) => setPassword(e.target.value)}
					type="password"
					placeholder="Password"
					className="loginInput"
				/>
								<Link to='/password-reset'>
				<p className='loginForgetText'>Forgot your password
								
				</p>
				</Link>

				<br />
				 <input type="submit" value="Login" onClick={loginUser}
				 className="loginInput"
				 /> 
				<br />
				<input type="submit" value="Create Account" onClick={createUser}
				
				className="loginInput"/> 
			</form>
			{/* </div> */}
			</div>
<Footer />
		</div>



  )
}

export default Login