import React from 'react'
import { trifoldwallet } from '../data'
import styled from 'styled-components'
import Product from './Product'
import TrifoldProductItem from './SubCategoryTrifoldWallets/TrifoldProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`



const TrifoldProducts = () => {
  return (
    <Container>

    {trifoldwallet.map((item)=>(
    
    <TrifoldProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default TrifoldProducts