import React from 'react'
import { chelseaProducts, flatBelts, linkBelts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import ChelseaProductItem from './SubCategoryChelsea/ChelseaProductItem'
import FlatProductItem from './SubCategoryFlatBelts/FlatProductItem'
import LinkProductItem from './SubCategoryLinkBelts/LinkProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`
const LinkProducts = () => {
  return (
    <Container>

    {linkBelts.map((item)=>(
    
    <LinkProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default LinkProducts