import React from 'react'
import styled from 'styled-components'
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import MapIcon from '@mui/icons-material/Map';
import PhoneIcon from '@mui/icons-material/Phone';
import RoomIcon from '@mui/icons-material/Room';
import MailOutlineIcon from '@mui/icons-material/MailOutline';



import { Link } from 'react-router-dom';

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  background-color: teal;
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 20px;
`;

const Logo = styled.h1``;

const Desc = styled.p`
  margin: 20px 0px;
  text-align: left;
  color: white;
`;

const SocialContainer = styled.div`
  display: flex;
`;

const SocialIcon = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: white;
  background-color: #${(props) => props.color};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
`;

const Center = styled.div`
  flex: 1;
  padding: 20px;
`;

const Title = styled.h3`
  margin-bottom: 30px;
`;

const List = styled.ul`
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
`;

const ListItem = styled.li`
  width: 50%;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
`;

const Right = styled.div`
  flex: 1;
  padding: 20px;
`;

const ContactItem = styled.div`
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  color: white;
`;

const Payment = styled.img`
  width: 50%;
`;

const Footer = () => {
  return (
   <Container>
   <Left>
  <Logo>Creative </Logo> 
  <Desc>
    
  n publishing and graphic design, Lorem ipsum is a placeholder text commonly used to demonstrate the visual form of a document or a typeface without relying on meaningful content. Lorem ipsum may be 
  used as a placeholder before final copy is available.
    </Desc>  
    <SocialContainer>
        <SocialIcon color="385999">
          <FacebookIcon/>
        </SocialIcon>
        <SocialIcon color="E4405F">
          <InstagramIcon/>
        </SocialIcon>

        <SocialIcon color="E50023">
          <TwitterIcon/>
        </SocialIcon>

    </SocialContainer>

   </Left>
<Center>
<Title>Useful links</Title>


<List>

        
          <ListItem>
          <Link to="/sizeguide" className='linkoflistitem'>
            Size guide
            </Link>
            </ListItem>
       
            
          <ListItem>
          <Link to="/returnpolicy" className='linkoflistitem'>
            Return policy
            </Link>
            </ListItem>
         
          <ListItem>
          <Link to="/privacypolicy" className='linkoflistitem'>
            Privacy policy
            </Link>
            </ListItem>
          
          
          <ListItem>
            <Link to="/termsconditions" className='linkoflistitem'>

           
            Terms and conditions
            </Link>
            </ListItem>
         
          
          
{/*           
          <ListItem>
            <Link to="/newarrivals" className='linkoflistitem'>

            New arrivals
            </Link>
            </ListItem> */}
          
          <ListItem>
            <Link to="/ordercancel" className='linkoflistitem'>

            Order cancellation
            </Link>
            </ListItem>
          
          <ListItem>
            <Link to="/aboutus" className='linkoflistitem'>

            Abous us
            </Link>
            </ListItem>
         
          <ListItem>
            <Link to="/contactus" className='linkoflistitem'>
            Contact us
            </Link>
            </ListItem>
          
          
          
        </List>
</Center>
<Right>
<Title >
    Contact
</Title>

<ContactItem >
          <RoomIcon style={{marginRight:"10px",color:"blue"}}/> E-9 islamabad
        </ContactItem>
        <ContactItem >
          <PhoneIcon style={{marginRight:"10px" ,color:"blue"}}/> 03410188982
        </ContactItem>
        <ContactItem >
          <MailOutlineIcon style={{marginRight:"10px" , color:"red"}} />creative@gmail.com
        </ContactItem>
        <Payment src="https://i.ibb.co/Qfvn4z6/payment.png" />





</Right>
   </Container>
  )
}

export default Footer