import React,{useState} from 'react'
import Announcement from '../Announcement';

import { Add, Remove } from "@material-ui/icons";
import Navbar from '../Navbar';
import Footer from '../Footer';

import styled from 'styled-components'
import { Navigate, useParams } from 'react-router-dom';
import { derbyProducts } from '../../data';

import {useLocation} from 'react-router-dom';

import { useNavigate } from 'react-router-dom';





const Container = styled.div`
  width: 100%;

`;

const Wrapper = styled.div`
  padding: 20px;
`;

const Title = styled.h1`
  font-weight: 300;
  text-align: center;
`;

const Top = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;

  @media only screen and (max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
`;

const TopButton = styled.button`
  padding: 10px;
  font-weight: 600;
  cursor: pointer;
  border: ${(props) => props.type === "filled" && "none"};
  background-color: ${(props) =>
    props.type === "filled" ? "black" : "transparent"};
  color: ${(props) => props.type === "filled" && "white"};
`;

const TopTexts = styled.div``;
const TopText = styled.span`
  text-decoration: underline;
  cursor: pointer;
  margin: 0px 10px;
`;

const Bottom = styled.div`
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
`;

const Info = styled.div`
  flex: ;
`;

const Product = styled.div`
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 20px;
`;

const ProductDetail = styled.div`
  flex: 2;
  display: flex;
  flex-wrap: wrap;
`;

const Image = styled.img`
  max-width: 100%;
  height: auto;
  background-color: red;

  @media screen and (max-width: 768px) {
    width: 100%;
    height: 80%;
    margin-top: 50px;
  }
`;

const Details = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
`;

const ProductName = styled.span``;

const ProductId = styled.span``;

const ProductColor = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: ${(props) => props.color};
`;

const ProductSize = styled.span``;

const PriceDetail = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const ProductAmountContainer = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;
`;

const ProductAmount = styled.div`
  font-size: 24px;
  margin: 5px;
`;

const ProductPrice = styled.div`
  font-size: 30px;
  font-weight: 200;
`;

const Hr = styled.hr`
  background-color: #eee;
  border: none;
  height: 1px;
`;

const Summary = styled.div`
  flex: 1;
  border: 0.5px solid lightgray;
  border-radius: 10px;
  padding: 20px;
  height: auto;
  max-width: 400px;

  overflow-y: auto;

  @media only screen and (max-width: 768px) {
    margin-top: 40px;
    height: 280px;
  }

  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    margin-top: 40px;
    height: 280px;
  }

  @media only screen and (min-width: 1025px) and (max-width: 1440px) {
    margin-top: 40px;
    height: 280px;
  }

  @media only screen and (min-width: 1441px) {
    margin-top: 30px;
    height: 250px;
  }
`;

const SummaryTitle = styled.h1``;

const SummaryItem = styled.div`
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  font-weight: ${(props) => props.type === 'total' && '500'};
  font-size: ${(props) => props.type === 'total' && '24px'};
`;

const SummaryItemText = styled.span``;

const SummaryItemPrice = styled.span``;

const Button = styled.button`
  width: 100%;
  padding: 10px;
  background-color: black;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: gray;
  }
`;






const DerbyCart = ({navigation}) => {
    const navigate = useNavigate();
    const { id,price,size,color } = useParams();
    const product_s = derbyProducts.find(x => Number(x.id) === Number(id));
console.log(product_s)
    const location=useLocation();
    const [productQty,setProductQty]=useState(location.state.quantity)
    const [updatedPrice,setUpdatedPrice]=useState(location.state.price)
    const sizeSelected=location.state.size;


const checkOutNavigate=()=>{
  navigate(`/cart/${product_s.id}/checkout`,
  {state:
    {
      price:updatedPrice,
      size:product_s.size,
      id:product_s.id,
      image:product_s.img,
      size:sizeSelected

    }

})
}



const addItems=()=>{
  console.log("i am click")
  setUpdatedPrice(0)
setProductQty(productQty+1);
console.log(productQty)
 setUpdatedPrice((1+productQty)*product_s.price)
 console.log(updatedPrice)
// console.log(updatedPrice)

 }

 const removeItems=()=>{
  if(productQty>1)
  {
    setUpdatedPrice(0)
    setProductQty(productQty-1);
    console.log(productQty)
     setUpdatedPrice((productQty-1)*product_s.price)
     console.log(updatedPrice)
  // setUpdatedPrice(productQty*product_s.price)
  // console.log(productQty)
  // console.log(updatedPrice)

  }
}


  return (
    <Container>
      <Announcement />
      <Navbar />
      
      <Wrapper>
        <Title>Your shoes</Title>
        <Top>
          <TopTexts>
            <TopText>Shopping Bag(0)</TopText>
            <TopText>Your Wishlist (0)</TopText>
          </TopTexts>
          <TopButton type="filled" onClick={()=>checkOutNavigate()}>CHECKOUT NOW</TopButton>
        </Top>
        <Bottom>
          <Info>
            <Product>
              <ProductDetail>
                <Image src={product_s.img} />
                <Details>
                  <ProductName>
                    <b>Product:</b> {location.state.name}
                  </ProductName>
                  <ProductId>
                    <b>ID:</b> 93813718293
                  </ProductId>
                  <ProductSize>
                    <b>Size:</b> {location.state.size}
                  </ProductSize>
                </Details>
              </ProductDetail>
              <PriceDetail>
                <ProductAmountContainer>
                <Remove onClick={removeItems}/>
                <ProductAmount>{productQty}</ProductAmount>
                <Add onClick={addItems} />
                </ProductAmountContainer>
                <ProductPrice>{updatedPrice}</ProductPrice>
              </PriceDetail>
            </Product>
            <Hr />
            </Info>
            <Summary>
            <SummaryTitle>ORDER SUMMARY</SummaryTitle>
            <div className='summaryItem'>
            <p className='summaryItemText'>Subtotal</p>
             <p className='summaryItemPrice'>{updatedPrice}</p>
             </div>
             <div className='summaryItem'>
             <p className='summaryItemText'>Size</p>
             <p className='summaryItemPrice'>{sizeSelected}</p>
</div>

<div className='summaryItem'>
             <p className='summaryItemText'>Quantity</p>
             <p className='summaryItemPrice'>{productQty}</p>
</div>

             </Summary>
            
          
        </Bottom>
      </Wrapper>
      <Footer />
    </Container>


  )
}

export default DerbyCart