import React from 'react'
import Announcement from '../Announcement'
import Navbar from '../Navbar'
import Footer from '../Footer'

const AboutUs = () => {
  return (
    <div>

<Announcement />
        <Navbar />
        <div className='orderCancelMain'>
        <h2 className='aboutusHeader'>
About Us
        </h2>

        <p className='ordercanceltext'>
        Established back in 2021, Creative Handmade Creations has always focused to be more customer centric by designing the products that matches to the expectation of our customers and therefore is progressing with each passing day. Our craftsmen and top notch material has always given us an upper hand. Our focal point is attention to detail to every single pair. Selection and processing of long lasting and fine quality leather is done by experts and 
        this premium quality leather is always sought from the well renowned tanneries. Brushing, creaming and polishing is carried out with imported
         creams that are applied meticulously and hence gives a more scintillate look


        </p>
    </div>

    <Footer />
    </div>
  )
}

export default AboutUs