import React from 'react'
import {  jodhpurProducts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import JodhpurProductItem from './SubCategoryJodhphur/JodhpurProductItem'

const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`

const JodhpurProducts = () => {
  return (
    <Container>

    {jodhpurProducts.map((item)=>(
    
    <JodhpurProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>



  )
}

export default JodhpurProducts