import React,{useState} from 'react'
import { useParams } from 'react-router-dom';
import styled from 'styled-components'
import Announcement from '../Announcement';

import Footer from '../Footer';
import { Add, Remove } from "@material-ui/icons";
import Navbar from '../Navbar';
import NewsLetter from '../NewsLetter';
import Cart from '../../pages/Cart';
import { useNavigate } from 'react-router-dom';
import {  chelseaProducts, creditCard, flatBelts, jodhpurProducts, loaferProducts, slimWallet } from '../../data';

const Container=styled.div`

`
const Wrapper=styled.div`
padding: 50px;
  display: flex;
  @media screen and (max-width: 768px) {
    flex-direction: column;
  }

`


const ImgContainer = styled.div`
  flex: 1;

  @media screen and (max-width: 768px) {
    flex-basis: 100%;
    height: auto;
  }
  
`;


const Image = styled.img`
  width: 100%;
  height: 90vh;
  object-fit: cover;
`;



const InfoContainer = styled.div`
  flex: 1;
  padding: 0px 50px;
`;




const Title = styled.h1`
  font-weight: 200;
`;

const Desc = styled.p`
  margin: 20px 0px;
`;

const Price = styled.span`
  font-weight: 100;
  font-size: 40px;
`;


const FilterContainer = styled.div`
  width: 50%;
  margin: 30px 0px;
  display: flex;
  justify-content: space-between;
`;

const Filter = styled.div`
  display: flex;
  align-items: center;
`;

const FilterTitle = styled.span`
  font-size: 20px;
  font-weight: 200;
`;

const FilterColor = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: ${(props) => props.color};
  margin: 0px 5px;
  cursor: pointer;
`;



const FilterSize = styled.div`
display: flex;
align-items: center;
margin-bottom: 10px;
`;

const Label = styled.label`
margin-right: 10px;
font-size: 16px;
`;

const Select = styled.select`
padding: 8px;
font-size: 16px;
border: 1px solid #ccc;
border-radius: 4px;

&:focus {
outline: none;
border-color: #1e90ff;
box-shadow: 0 0 0 2px rgba(30, 144, 255, 0.2);
}
`;

const Option = styled.option`
font-size: 16px;
`;


const FilterSizeOption = styled.option``;

const AddContainer = styled.div`
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const AmountContainer = styled.div`
  display: flex;
  align-items: center;
  font-weight: 700;
`;

const Amount = styled.span`
  width: 30px;
  height: 30px;
  border-radius: 10px;
  border: 1px solid teal;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 5px;
`;

const Button = styled.button`
  padding: 15px;
  border: 2px solid teal;
  background-color: white;
  cursor: pointer;
  font-weight: 500;
  &:hover{
      background-color: #f8f4f4;
  }
`;


const SlimProductScreen = () => {
  const { id } = useParams();

  const [productQty,setProductQty]=useState(1)

    const navigate = useNavigate();
  
    const product_s = slimWallet.find(x => Number(x.id) === Number(id));
    const [updatedPrice,setUpdatedPrice]=useState(product_s.price)

    const addItems=()=>{
      setUpdatedPrice(0)
    setProductQty(productQty+1);
    console.log(productQty)
     setUpdatedPrice((1+productQty)*product_s.price)
     console.log(updatedPrice)
  
    }

    const navigateToCartPage=()=>{
  
      navigate(`/slimwallet/${product_s.id}`,
        
      {state:
        {
          price:updatedPrice,
          size:product_s.size,
          name:product_s.title,
          quantity:productQty
        }
        
      })
         }
  
  
  
  
  
        

const removeItems=()=>{
  if(productQty>1)
  {
    setUpdatedPrice(0)
    setProductQty(productQty-1);
    console.log(productQty)
     setUpdatedPrice((productQty-1)*product_s.price)
     console.log(updatedPrice)
  // setUpdatedPrice(productQty*product_s.price)
  // console.log(productQty)
  // console.log(updatedPrice)

  }
}

  return (
<Container>
    <Announcement />
    <Navbar />

    <Wrapper>
    <ImgContainer>
    <Image src={product_s.img}/>
    </ImgContainer>
    <InfoContainer>
      <Title>
       {product_s.title}
      </Title>
<Desc>
{product_s.description}
</Desc>
<Price>
{product_s.price}
</Price>
{/* <FilterContainer> */}
{/* <Filter>
        <FilterTitle>
Color
        </FilterTitle>
     <FilterColor color={product_s.color[0]} />
     <FilterColor color={product_s.color[1]} />
     <FilterColor color={product_s.color[2]} />
    </Filter> */}

    {/* <Filter>
        <FilterTitle>
Size
        </FilterTitle>
    
    <FilterSize>
    <FilterSizeOption>Belt size</FilterSizeOption>
                <FilterSizeOption>{product_s.size[0]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[1]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[2]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[3]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[4]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[5]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[6]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[7]}</FilterSizeOption>
                <FilterSizeOption>{product_s.size[8]}</FilterSizeOption>
    </FilterSize> */}



    {/* </Filter>



</FilterContainer> */}
<h4>Rs {updatedPrice}</h4>
<AddContainer>
<AmountContainer>
              <Remove onClick={removeItems}/>
              <Amount >{productQty}</Amount>
              <Add onClick={addItems}/>
            </AmountContainer>
            <Button onClick={navigateToCartPage}>ADD TO CART</Button>
            </AddContainer>
     </InfoContainer>
    </Wrapper>



    <NewsLetter />
     <Footer />
    </Container>
    
    )
}

export default SlimProductScreen