const Container = styled.div`
  margin-top: 20px;
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
  @media screen and (max-width: 768px) {
height: 770px;

  }
`;

const Arrow = styled.div`
  width: 50px;
  height: 50px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: ${(props) => props.direction === "left" && "10px"};
  right: ${(props) => props.direction === "right" && "10px"};
  margin: auto;
  cursor: pointer;
  opacity: 0.5;
  z-index: 2;
  
  @media (max-width: 768px) {
    width: 30px;
    height: 30px;
    font-size: 10px;
  }
`;

const Wrapper = styled.div`
  height: 100%;
  display: flex;
  transition: all 1.5s ease;
  transform: translateX(${(props) => props.slideIndex * -100}vw);
  
  @media (max-width: 768px) {
    flex-wrap: nowrap;
  }
`;

const Slide = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  background-color: #${(props) => props.bg};
  
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const ImgContainer = styled.div`
  height: 100%;
  flex: 1;
`;

const Image = styled.img`
  height: 80%;
`;

const InfoContainer = styled.div`
  flex: 1;
  padding: 50px;
  
  @media (max-width: 768px) {
    padding: 20px;
  }
`;

const Title = styled.h1`
  font-size: 70px;
  
  @media (max-width: 768px) {
    font-size: 50px;
    text-align: center;
  }
`;

const Desc = styled.p`
  margin: 50px 0px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 3px;
  
  @media (max-width: 768px) {
    margin: 20px 0px;
    font-size: 16px;
    text-align: center;
  }
`;

const Button = styled.button`
  padding: 10px;
  font-size: 20px;
  background-color: transparent;
  cursor: pointer;
  
  @media (max-width: 768px) {
    padding: 5px;
    font-size: 12px;
      }
`;



const Slider = () => {
  const navigate = useNavigate();

  const [slideIndex,setSlideIndex]=useState(0)
  const handleClick=(direction)=>{

if(direction=="left"){
    setSlideIndex( slideIndex>0 ? slideIndex-1 : 2);
}
else {
    setSlideIndex( slideIndex<2 ? slideIndex+1: 0)
}

  };
  
  
  
const handleClickCategory=(itemid)=>{
 console.log(itemid)
  if(itemid==1)
  {

  
    navigate('/formals');

  }
  if(itemid==2)
  {

 navigate('/round');
  }
  if(itemid==3)
  {
    navigate('/slim');

  }

}
    return (
   <Container>
    
    <Arrow direction="left" onClick={()=>handleClick("left")}>

   <ArrowLeftIcon />

    </Arrow>

<Wrapper slideIndex={slideIndex}>
{sliderItems.map((item)=>(

<Slide bg={item.bg} key={item.id}>

<ImgContainer>
<Image src={item.img}/>

</ImgContainer>

<InfoContainer>
<Title>{item.title}</Title>
<Desc>{item.desc}</Desc>
<Button onClick={()=>handleClickCategory(item.id)}>Shop now</Button>

</InfoContainer>
</Slide>

))}

</Wrapper>
 
    <Arrow direction="right" onClick={()=>handleClick("right")}>

   <ArrowRightIcon />
   
    </Arrow>


   </Container>
  )
}

export default Slider