import React from 'react'
import { chelseaProducts, flatBelts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import ChelseaProductItem from './SubCategoryChelsea/ChelseaProductItem'
import FlatProductItem from './SubCategoryFlatBelts/FlatProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`
const Flatproducts = () => {
  return (
    <Container>

    {flatBelts.map((item)=>(
    
    <FlatProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>

  )
}

export default Flatproducts