const ChelseaProductScreen = () => {
  const [productQty,setProductQty]=useState(1)
  const [sizeis,setSelectedSize]=useState("")


  const addItems=()=>{
    setUpdatedPrice(0)
  setProductQty(productQty+1);
  console.log(productQty)
   setUpdatedPrice((1+productQty)*product_s.price)
   console.log(updatedPrice)
  // console.log(updatedPrice)

   }
    const { id } = useParams();

    
    const navigate = useNavigate();
  
    const product_s = chelseaProducts.find(x => Number(x.id) === Number(id));
    const [updatedPrice,setUpdatedPrice]=useState(product_s.price)

    const navigateToCartPage=()=>{
      if(sizeis==="" || sizeis==="Choose your size" )
      {
      alert("please select size")
    return false;
      }
      navigate(`/chelseacart/${product_s.id}`,
        
      {state:
        {
          price:updatedPrice,
          size:product_s.size,
          name:product_s.title,
          quantity:productQty,
          size:sizeis
        }
        
      })
         }
  
  
  
         const removeItems=()=>{
          if(productQty>1)
          {
            setUpdatedPrice(0)
            setProductQty(productQty-1);
            console.log(productQty)
             setUpdatedPrice((productQty-1)*product_s.price)
             console.log(updatedPrice)
          // setUpdatedPrice(productQty*product_s.price)
          // console.log(productQty)
          // console.log(updatedPrice)
        
          }
        }
  
       
  return (
    <Container>
    <Announcement />
    <Navbar />

    <Wrapper>
    <ImgContainer>
    <Image src={product_s.img}/>
    </ImgContainer>
    <InfoContainer>
      <Title>
       {product_s.title}
      </Title>
<Desc>
{product_s.description}
</Desc>
<Price>
{product_s.price}
</Price>


<FilterSize>
      <Label>UK Size</Label>
      <Select onChange={(e) => {
        setSelectedSize(e.target.value);
        console.log(`Selected size: ${e.target.value}`);
      }}>
        <Option selected disabled>Choose your size</Option>
        {product_s.size.map((size, index) => (
          <Option key={index} value={size}>{size}</Option>
        ))}
      </Select>
    </FilterSize>

<h4>Rs {updatedPrice}</h4>
<AddContainer>
            <AmountContainer>
              <Remove onClick={removeItems}/>
              <Amount>{productQty}</Amount>
              <Add  onClick={addItems}/>
            </AmountContainer>
            <Button onClick={navigateToCartPage}>ADD TO CART</Button>
          </AddContainer>

     </InfoContainer>
    </Wrapper>



    <NewsLetter />
     <Footer />
    </Container>


  )
}

export default ChelseaProductScreen