import React from 'react'
import { oxfordProducts, wholecutProducts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import OxfordProductItem from './SubCategoryOxford/OxfordProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`




const OxfordProducts = () => {
  return (
    <Container>

    {oxfordProducts.map((item)=>(
    
    <OxfordProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default OxfordProducts