import React from 'react'
import styled from 'styled-components'



const Container=styled.div`

height:30px;
width: 100%;
background-color:teal;
color:white;
display:flex;
align-items:center;
justify-content:center;
font-size:14px;
font-weight:500;

`



const Announcement = () => {
  return (
    
   <Container>

    Free shipping on orders over 4000 pkr
   </Container>



  )
}

export default Announcement