import React, { useEffect } from 'react'
import { useState } from 'react'

import { navigate, useNavigate, useParams } from 'react-router-dom';
import Navbar from '../../src/components/Navbar'
import Footer from '../../src/components/Footer'
import '../../src/App.css'
import { Link } from 'react-router-dom';
import '../../src/App.css'
import { TrendingUpRounded } from '@material-ui/icons';

const ForgotPassword = () => {

  const {id,token}=useParams();
  // const history=useNavigate()


    const [password, setPassword] = useState('')
	const navigate = useNavigate();

const [message,setMessage]=useState("false")

    const sendLink=async(e)=>{
        console.log("sending password request")
         e.preventDefault();
        //  navigate("/login")
         
   const res=await fetch(`http://localhost:5000/api/${id}/${token}`,{
    method:"POST",
    headers:{
      "Content-Type":"application/json"
    },
     body:JSON.stringify({password})
             
  
     });


  const data=await res.json()
  console.log(data)
if(data.status==200){
    setPassword("")
    setMessage(true)
    alert("password updated successfully, lets login now")
    navigate("/login")

}
else {

alert("token expired generate link again")
}



    }

   
     const userValid=async()=>{


   const res=await fetch(`http://localhost:5000/forgotpassword/${id}/${token}`,{
  method:"GET",
  headers:{
    "Content-Type":"application/json"
  },

			// body: JSON.stringify({
			// 	password
			// }),


   });

   const data=await res.json()

   if(data.status==200){
    console.log("user valid")
   }
   else {
    navigate("/error")
   }


     }




  useEffect(()=>{
    console.log("i am running");
    userValid()
},[])





  return (
 <div>
<Navbar />
{/* 
{setMessage ? 
navigate("/login")

:""} */}
<form >
    <div className='forgotPassHeader'>
        Reset your password
    </div>
    <label htmlFor='labelHead'>New password</label>
		<br />
        		<input
					value={password}
					onChange={(e) => setPassword(e.target.value)}
					type="password"
					placeholder="New password"
					className="loginInput"
				/>


<button className='forgotBtn' onClick={sendLink}>
    Send
</button> 

                </form>


<Footer />
 </div>


    )
}

export default ForgotPassword