import React from 'react'
import Announcement from '../Announcement'
import Footer from '../Footer'
import Navbar from '../Navbar'

const OrderCancellation = () => {
  return (
    <div>

        <Announcement />
        <Navbar />
        <div className='orderCancelMain'>
        <h2 className='orderCancelHeader'>
        ORDER CANCELLATION POLICY

        </h2>

        <p className='ordercanceltext'>
        If there is a problem and you don't like our services/shoe please call us at our toll free number to cancel your order.


        </p>

        <br />
        <h4 className='numberOrder'>03410188982</h4>
</div>
        <Footer />
    </div>
  )
}

export default OrderCancellation