import React from 'react'
import { useState } from 'react'

import { useNavigate } from 'react-router-dom';
import Navbar from '../../src/components/Navbar'
import Footer from '../../src/components/Footer'
import '../../src/App.css'
import { Link } from 'react-router-dom';


const PasswordReset = () => {
    const [email, setEmail] = useState('')
	const navigate = useNavigate();
   const [message,setMessage]=useState("");


    const sendLink=async (e)=>{
        console.log("sending link")
         e.preventDefault();

         const response = await fetch('http://localhost:5000/api/sendpasswordlink',
          {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
			},
			body: JSON.stringify({
				email
            
            }),
		})


      console.log(response);
      const data=await response.json();
      if(data.status==200)
      {
        setEmail("");
        setMessage(true)
      }
else{
console.log("invalid user")


}


    }
  return (
 <div>
<Navbar />
<div className='forgotPassHeader'>
        Reset your password
    </div>
{message ? <p style={{color:"green",fontWeight:"bold"}}>
    Password reset link sent successfully in your
    email
</p>:""}
<form >
    
				<input
					value={email}
					onChange={(e) => setEmail(e.target.value)}
					type="email"
					placeholder="Email"
					className="loginInput"
				/>


<button className='forgotBtn' onClick={sendLink}>
    Send
</button>

                </form>


<Footer />



 </div>


    )
}

export default PasswordReset