import React from 'react'
import { derbyProducts, loaferProducts, wholecutProducts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import LoaferProductItem from './SubCategoryLoafer/LoaferProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`

const LoafersProducts = () => {
  return (
    <Container>

    {loaferProducts.map((item)=>(
    
    <LoaferProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default LoafersProducts