import React from 'react'
import { derbyProducts, wholecutProducts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import DerbyProductScreen from './SubCategoryDerby/DerbyProductScreen'
import DerbyProductItem from './SubCategoryDerby/DerbyProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`



const DerbyProducts = () => {
  return (
    <Container>

    {derbyProducts.map((item)=>(
    
    <DerbyProductItem item={item} key={item.id} />
    ))}
    
    
        </Container>
  )
}

export default DerbyProducts