import React from 'react'
import { chelseaProducts } from '../data'
import styled from 'styled-components'
import Product from './Product'
import ChelseaProductItem from './SubCategoryChelsea/ChelseaProductItem'


const Container=styled.div`
padding: 20px;
display: flex;
flex-wrap: wrap;

`

const Chelseaproducts = () => {
  return (
    <Container>

{chelseaProducts.map((item)=>(

<ChelseaProductItem item={item} key={item.id} />
))}


    </Container>
  )
}

export default Chelseaproducts